@import "../public_func.sass";

main {
	margin-right: auto;
	margin-left: auto;
	line-height: 1.5em;
	letter-spacing: 0.04em;
	section {
		width: 100%;
		height: 100vh;
		float: left;
		[class ^= title] {
			text-transform: uppercase;
			font-weight: bold; }
		&>h1 {
			padding: rem(100);
			padding-top: rem(120);
			background: #556655;
			color: white; }
		article {
			box-sizing: border-box;
			max-width: rem(1300);
			width: 100%;
			padding: 0 rem(20);
			margin-right: auto;
			margin-left: auto;
			font-weight: 500;
			&:first-child {
				padding-top: rem(20); }
			padding-top: rem(50);
			h2.title {
				font-size: rem(30); }
			a {
				color: #48a0ef;
				font-size: rem(18);
				font-weight: bold;
				text-decoration: none;
				&:hover {
					color: #0069c8; } } } } }
